.mainpage_header_title{
  width: 100%;
  height: 8vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.mainpage_header_category{
  height: 100%;
  width: 100%;
}
.mainpage_header_category nav {
  height: 5vh;
  width: 100%;
}

.mainpage_header_category ul {
  justify-content: space-evenly;
  align-items: center;
  border: 1px solid black;
  height: 100%;
  display: flex;
  list-style: none;
  padding: 0;
  width: 100%; 
  
}

.mainpage_header_category li {
  min-width: 50px;
  height: 40px;
  line-height: 40px;
  text-align: center;
  margin-right: 10px;
  transition: all 0.3s  ease-in-out;
  position: relative; /* 부모 요소로부터의 상대적인 위치를 설정합니다. */
  
}
.mainpage_header_category li:hover {
  cursor: pointer;
  transform: scale(1.1); /* 'scale' 속성을 'transform' 속성으로 수정합니다. */
}

.mainpage_header_category li:hover::before {
  content: '•'; /* 가상 요소 '::before'에 표시할 내용을 설정합니다. */
  position: absolute; /* 점을 부모 요소 내에서 절대 위치로 설정합니다. */
  top: -15px; /* 위쪽으로 10px 이동하여 글자 위에 위치시킵니다. */
  left: 50%; /* 가운데 정렬합니다. */
  transform: translateX(-50%); /* 왼쪽으로 50%만큼 이동하여 가운데 정렬합니다. */
  font-size: 20px; /* 점의 크기를 설정합니다. */
}
.slidecontainer{
    display: flex;
    justify-content: center;
    margin: 0 auto;
}
.slideshow{
    width: 80%;
    height: 70vh;
    text-align: center;
    font-size: 40px;
    overflow: hidden;
}

.slideimages img{
  border-radius: 5%;
  max-width: 100%;
  max-height: 100%;
  object-fit: cover;
}
.slideimages{
  width: 100%;
  height: 100%;
  flex-shrink: 0; /*이 구역안에 생기는 영역들이 공간을 분배하여 가지지 않도록 적용*/
  font-size: 40px;
  text-align: center;
    position: relative;
 
}
.slideButtons{
    border: 1px solid black;
   height: inherit;
}
.slideimages button {
  margin: 5px;
}